import {Component, PropTypes, default as React} from 'react'
import ReactDOM from 'react-dom'
import Moment from 'moment'
import _ from 'underscore'
import ClassSet from 'classnames'

const dateFormat = "YYYY-MM-DD"

class Day extends Component {
    constructor(props) {
        super(props)
    }
    
    clickedItem(i) {
        return (event) => {
            console.log("Clicked " + i)
            
            let item = this.props.items[i]
            let {_id} = item
            
            // this.context.router.push("/workorders/" + _id)
        }
    }
    
    render() {
        let className = ClassSet({
            day: true,
            weekend: this.props.weekend,
            today: this.props.today
        })
        
        let displayValue = this.props.value
        
        if (this.props.monthName) {
            displayValue = this.props.monthName + " " + displayValue
        }
        
        if (!this.props.inMonth) {
            className += " outOfMonth"
        }
        
        return <div className={ className }>
            <div className="badge">
                <div className="label">{ displayValue }</div>
            </div>
            <div className="items">
            {
                this.props.items.map((item, i) => {
                    let itemKey = "item" + i
                    
                    return <div className="item" key={ itemKey } onClick={ this.clickedItem(i) }>{ item.description }</div>
                })
            }
            </div>
        </div>
    }
}

Day.defaultProps = {
    items: []
}

class Week extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return <div className="week">{
            this.props.days.map(function(day, i) {
                let events = day.events.splice(0)
                
                if (events.length > 3) {
                    events = _.first(events, 3)
                }
                
                let props = _.clone(day)
                props.items = events
                props.inMonth = day.dayIsInMonth
                props.key = "day" + i
                
                delete props.events
                delete props.dayIsInMonth
                
                return <Day {...props}></Day>
            })
        }</div>
    }
}

class WeekHeader extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return <div className="weekHeader">{
            this.props.days.map(function(day, i) {
                let className = "dayOfWeek"
                
                if (i == 0 || i == 6) {
                    className += " weekend"
                }
                
                return <div className={ className } key={ "week" + i }>{ day }</div>
            })
        }</div>
    }
}

const preventDefault = function(event) {
    event.preventDefault()
}

class Calendar extends Component {
    constructor(props) {
        super(props)

        // this.state = {
        //     eventsByDay: {}
        // }

        // this.fetchItems()

        // this._onChange = (event) => {
        //     this.setState({
        //         eventsByDay: CalendarStore.eventsByDay
        //     })
        // }
    }

    componentDidMount() {
        // CalendarStore.addChangeListener(this._onChange)
        
        // this.fetchItems()
    }

    componentWillUnmount() {
        // CalendarStore.removeChangeListener(this._onChange)
    }

    // componentDidUpdate(prevProps, prevState) {
    //     if (prevProps.params.id != this.props.params.id) {
    //         console.log("Did update")
    //         // this.fetchItems()
    //     }
    // }

    goToDate(date) {
        this.props.onChange(date)
        // this.context.router.push("/calendar/" + date.format(dateFormat))
    }

    goBackMonth() {
        let newDate = this.getCurrentDate().startOf('month')
        newDate.subtract(1, 'month')
        this.goToDate(newDate)
    }
    
    goForwardMonth() {
        let newDate = this.getCurrentDate().startOf('month')
        newDate.add(1, 'month')
        this.goToDate(newDate)
    }
    
    goToToday() {
        this.goToDate(Moment())
    }
    
    // fetchItems() {
    //     let fromDate = this.getCurrentDate().startOf('month')
        
    //     let monthStartIndex = fromDate.day()
        
    //     if (monthStartIndex > 0) {
    //         fromDate.subtract(monthStartIndex, 'days')
    //     }
        
    //     let toDate = fromDate.clone()
    //     toDate.add(42, 'days')
        
    //     CalendarStore.fetchItems(fromDate, toDate)
    // }

    getCurrentDate() {
        return this.props.date
    }
    
    render() {
        let date = this.getCurrentDate().startOf('month')
        let todayKey = Moment().format(dateFormat)
        
        let monthStartIndex = date.day()
        let daysInMonth = date.daysInMonth()
        
        let currentDate = date.clone().subtract(monthStartIndex, 'day')
        let monthName = date.format("MMMM")
        let year = parseInt(date.format("YYYY"))
        let daysInLastMonth = 0;

        if (monthStartIndex > 0) {
            let lastDayOfLastMonth = date.clone().subtract(1, 'day')
            daysInLastMonth = lastDayOfLastMonth.daysInMonth()
        }
        
        let weeks = []
        let numberOfWeeks = 6
        
        let week = 0
        let dayOfMonth = 1
        let cellIndex = 0
        
        let daysInWeek = Moment.weekdaysShort()
        
        let monthDate = date.clone()
        
        for (week = 0; week < numberOfWeeks; week++) {
            let days = []
            let dayOfWeek = 0
            
            for (dayOfWeek = 0; dayOfWeek < 7; dayOfWeek++)
            {
                let dayOfMonth = (cellIndex - monthStartIndex) + 1
                let dayIsInMonth = true
                
                if (dayOfMonth < 1) {
                    dayOfMonth = daysInLastMonth + dayOfMonth
                    dayIsInMonth = false
                } else if (dayOfMonth > daysInMonth) {
                    dayOfMonth = (dayOfMonth - daysInMonth)
                    dayIsInMonth = false
                }
                
                let dayString = currentDate.format('YYYY-MM-DD')
                let events = []
                
                let day = {
                    value: dayOfMonth,
                    weekend: (dayOfWeek == 0 || dayOfWeek == 6),
                    today: (todayKey === dayString),
                    dayIsInMonth,
                    events
                    // events: this.state.eventsByDay[dayString] || []
                }
                
                if (dayOfMonth == 1) {
                    day.monthName = monthDate.format("MMM")
                    monthDate.add(1, 'month')
                }
                days.push(day)
                
                currentDate.add(1, 'day')
                cellIndex++
            }
            
            weeks.push(<Week days={ days } key={ "week" + week } />)
        }
        
        return <div className="calendarContainer">
            <div className="scrollContainer calendarScroll">
                <div className="calendar">{ weeks }</div>
            </div>
            <div className="calendarHeader">
                <div className="headerText">
                    <span className="month">{ monthName }</span> <span className="year">{ year }</span>
                </div>
                <div className="headerButtons">
                    <div className="button" onClick={ this.goBackMonth.bind(this) } onMouseDown={ preventDefault }><i className="fa fa-angle-left"/></div>
                    <div className="button" onClick={ this.goToToday.bind(this) } onMouseDown={ preventDefault }>Today</div>
                    <div className="button" onClick={ this.goForwardMonth.bind(this) } onMouseDown={ preventDefault }><i className="fa fa-angle-right"/></div>
                </div>
                <div className="weekdayTable">
                    <WeekHeader days={ daysInWeek } key="weekHeader"/>
                </div>
            </div>
        </div>
    }
}

function dateChanged(date) {
    console.log("Date changed")
    console.log(date)
}

ReactDOM.render(<Calendar date={ Moment() } onChange={ dateChanged }/>, document.getElementById('calendar'))

